            <div class="row">
                <div class="col-md-1">
                </div>
                <div class="col-md-10">
                    <b-tabs>
                        <b-tab title="Общее" active>
                            <expznania></expznania>
                        </b-tab>
                        <b-tab title="Резюме">
                            <expresume></expresume>
                        </b-tab>
                    </b-tabs>
                </div>
                <div class="col-md-1">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import ExpZnania from '@/components/ExpZnania'
import ExpResume from '@/components/ExpResume'

export default {
  name: 'experience',
  components: {
    expznania: ExpZnania,
    expresume: ExpResume
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



