    <div id="wrap">
    <div id="main" class="container-fluid">      
      <mainmenu></mainmenu>
      <div class="row lpad">
        <div class="col-md-1">
        </div>
        <div class="col-md-10">
          <div class="container-fluid containerMod makeScroll roundborder">
            <router-view></router-view>
          </div>
        </div>
      </div>          
    </div>
    </div>
    <footer class="footer lpad">
      <span class="white"><h6 align="right">{{ version }}</h6></span>
      </footer>
  </div>
</template>

<script>
/*
      <div class="row footer navbar-fixed-bottom">
        <div class="col-md-1">
        </div>
          <div class="col-md-10">
            <nav class="navbar fixed-bottom">
              <p class="white" align="right"><b-button size="sm" variant='secondary' href="">GTG (C) 2017</b-button></p>
            </nav>
          </div>
      </div>
*/
import Vue from 'vue'
import BootstrapVue from 'bootstrap-vue'
Vue.use(BootstrapVue)
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import MainMenu from '@/components/MainMenu'

export default {
  name: 'app',
  data () {
    return {
      version: 'GTG (C) 2017 Ver. 1.0'
    }
  },
  components: {
    mainmenu: MainMenu
  }
}
