<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  margin-top: 60px;
}

.myJumbotron {
  background-color: #89AC76;
  /*#44944A;*/
  color: white;
  padding: 10px;
}

body {
  padding-bottom: 1px;
  padding-top: 1px;
}

.special-card {
  background-color: rgba(245, 245, 245, 1);
  opacity: .9;  
}

.makeScroll {
  height: 100%;
  min-height: 10vh;
  max-height: 80vh;
  /*overflow-y: scroll;*/
  overflow-y: auto;
  margin: auto;
}

.bottomFriendly {
  height: 80%;
  max-height: 80vh;
  /*overflow-y: scroll;*/
}

.limitHeight {
  max-height: calc(100% - 60px);
  overflow: auto;
}

.containerMod {
background-color: rgba(245, 245, 245, 1);
opacity: .9; 
margin: 0px auto;
}

/*.footer {
    position: fixed;
    height: 100px;
    bottom: 0;
    width: 100%;
    color: #FFFFFF;
}*/

.white {    
    color: #FFFFFF;    
}

.roundborder {
-moz-border-radius:4px;
-khtml-border-radius:4px;
-webkit-border-radius:4px;
border-radius:4px;
/*border:1px solid white;*/
}

html, body {
  height: 100%;
}

#wrap {
  min-height: 100%;
}

#main {
  overflow:auto;
  padding-bottom:150px; /* this needs to be bigger than footer height*/
}

.footer {
  position: relative;
  margin-top: -60px; /* negative value of footer height */
  height: 60px;
  clear:both;
  padding-top:11px;
} 

.lpad {
  padding-left:10em;
  padding-right:10em;
}

/* xs - устройства (до 576px) */
/* CSS для ширины, которая меньше 575px (включительно) */
 
/* sm-устройства (больше или равно 576px) */
@media (min-width: 576px) {
  /* CSS для: 576px <= ширины <= 767px */
  .lpad {
  padding-left:0em;
  padding-right:0em;

}
}
 
/* md-устройства (больше или равно 768px) */
@media (min-width: 768px) {
  /* CSS для: 768px <= ширины <= 991px */
    .lpad {
    padding-left:2em;
  padding-right:2em;

}
}
 
/* lg-устройства (больше или равно 992px) */
@media (min-width: 992px) {
  /* CSS для: 992px <= ширины <= 1119px */
    .lpad {
    padding-left:5em;
  padding-right:5em;

}
}
 
/* xl-устройства (больше или равно 1200px) */
@media (min-width: 1200px) {
  /* CSS для: ширины >= 1200px */
    .lpad {
    padding-left:10em;
    padding-right:10em;

}
}

</style>



