<template>
  <div class="expznania">
    <br>
    <h4>Мои навыки:</h4>
    <br>
    <!--h5>Языки: <span v-for="lang in langs"><b-badge variant="primary" pill>{{ lang }}</b-badge>&nbsp;</span></h5-->
    <p>
    <strong>Языки:</strong> <span v-for="(lang, index) in langs"><a class="article-category-link" href="javascript:void(0)">{{ lang }}</a><span v-if="index != langs.length-1">&nbsp;&middot;&nbsp;</span></span>    
    </p>

    <p>
    <strong>Опыт работы:</strong> <span v-for="(skill, index) in skills"><a class="article-category-link" href="javascript:void(0)">{{ skill }}</a><span v-if="index != skills.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>

    <p>
    <strong>Технологии:</strong> <span v-for="(tech, index) in techs"><a class="article-category-link" href="javascript:void(0)">{{ tech }}</a><span v-if="index != techs.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>

    <p>
    <strong>Вендоры:</strong> <span v-for="(vendor, index) in vendors"><a class="article-category-link" href="javascript:void(0)">{{ vendor }}</a><span v-if="index != vendors.length-1">&nbsp;&middot;&nbsp;</span></span>
    </p>
    <br>
    
  </div>
</template>

<script>
export default {
  name: 'expznania',
  data () {
    return {
      langs: ['C', 'C++', 'Golang', 'Assembler', 'C#', 'Java', 'Python', 'JavaScript', 'PL/SQL', 'HTML/CSS', 'Bash'],
      skills: ['DBA', 'System Administrator', 'Internet Banking', 'Banking Automation', 'Disaster Recovery', 'Business Continuity', 'Backup', 'Datacenter'],
      techs: ['Linux', 'Unix', 'Windows Server', 'Oracle Database', 'MSSQL', 'Hyper-V', 'Vmware ESXi', 'LTO', 'LTFS'],
      vendors: ['IBM', 'DELL', 'CISCO', 'APC']
    }
  }
}
</script>



